//#region Core Imports
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';

import { CommonModule } from '@angular/common';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { PopoverModule } from 'ngx-bootstrap/popover';
import { ModalModule } from 'ngx-bootstrap';

import { RestangularModule, Restangular } from 'ngx-restangular';

import { ToastrModule } from 'ngx-toastr';

import { OrderModule } from 'ngx-order-pipe';

// toaster
//import {ToastModule} from 'ng2-toastr/ng2-toastr';

//#endregion

//#region App Imports
import { AppRoutingModule } from './app-routing.module';

import { AppComponent } from './app.component';
import { HeaderComponent } from './component/header/header.component';
import { MainSectionComponent } from './component/main-section/main-section.component';
import { SidenavComponent } from './component/sidenav/sidenav.component';

// amenity
import { AmenityComponent } from './component/main-section/content/amenity/amenity.component';

// packages
import { PackagesComponent } from './component/main-section/content/packages/packages.component';

// unit manager
import { UnitManagerComponent } from './component/main-section/content/unit-manager/unit-manager.component';

// division 
import { DivisionComponent } from './component/main-section/content/division/division.component';
import { DivisionHomeComponent } from './component/main-section/content/division/division-home/division-home.component';
import { DivisionCreateComponent } from './component/main-section/content/division/division-create/division-create.component';

// shared body
import { SharedHeaderComponent } from './component/main-section/shared/shared-header/shared-header.component';
import { SharedBodyComponent } from './component/main-section/shared/shared-body/shared-body.component';
import { SharedFooterComponent } from './component/main-section/shared/shared-footer/shared-footer.component';

// home products
import { HomeProductComponent } from './component/main-section/content/home-product/home-product.component';
import { HomeProductHomeComponent } from './component/main-section/content/home-product/home-product-home/home-product-home.component';
import { HomeProductCreateComponent } from './component/main-section/content/home-product/home-product-create/home-product-create.component';

// floorplan
import { FloorPlanComponent } from './component/main-section/content/floor-plan/floor-plan.component';
import { FloorPlanHomeComponent } from './component/main-section/content/floor-plan/floor-plan-home/floor-plan-home.component';
import { FloorPlanCreateComponent } from './component/main-section/content/floor-plan/floor-plan-create/floor-plan-create.component';
import { UnitManagerHomeComponent } from './component/main-section/content/unit-manager/unit-manager-home/unit-manager-home.component';
import { UnitManagerSubComponent } from './component/main-section/content/unit-manager/unit-manager-sub/unit-manager-sub.component';

// amenity
import { AmenityHomeComponent } from './component/main-section/content/amenity/amenity-home/amenity-home.component';
import { AmenityCreateComponent } from './component/main-section/content/amenity/amenity-create/amenity-create.component';
import { PackagesHomeComponent } from './component/main-section/content/packages/packages-home/packages-home.component';

// 3d models
import { ThreeDModelsComponent } from './component/main-section/content/3d-models/3d-model.component';

import { FooterHeaderDataService } from './component/main-section/shared/footer-header-data.service';
import { DataShared } from './component/main-section/content/floor-plan/data';

//modal import
//import { ModalContentComponent } from './component/main-section/content/floor-plan/floor-plan-create/floor-plan-create.component';
// confirmation modal
import { ConfirmationModalComponent } from './component/main-section/shared/confirm-modal/confirm-modal';
import { ModalAddRoomComponent, ModalAddHomeDimensionComponent, ModalAddFeaturesComponent, ModalFacadeDesignComponent, ModalManageFeaturesComponent, ModalManageFloorComponent } from './component/main-section/content/floor-plan/floor-plan-create/floor-plan-create.component';

// widgets and search filters
import { PopupSearchComponent, SearchFilter } from './component/main-section/shared/popup-search/popup-search-component';

// directives
// only numebr directive
import { OnlyNumber } from './directives/only-numbers';
//#endregion

// environment
import { environment } from '../environments/environment';
// progress loader
import { ProgressProvider } from './component/main-section/shared/data/progress-provider';
import { ToastNotificationComponent } from './component/main-section/shared/toast-notification/toast-notification.component';

// rest
import { RestServices } from './services/rest/rest-services.service';
import { HomeProductEditComponent } from './component/main-section/content/home-product/home-edit/home-product-edit/home-product-edit.component';
import { DropdownMenuComponent } from './component/header/dropdown-menu/dropdown-menu.component';
import { AddHomeProductModalComponent } from './component/main-section/content/home-product/home-product-create/modal/add-home-product-modal/add-home-product-modal.component';
import { CreateDimensionComponent } from './component/main-section/content/floor-plan/floor-plan-create/widget-template/create-dimension/create-dimension.component';
import { FilterModalComponent } from './component/main-section/shared/shared-header/modal/filter-modal/filter-modal.component';
import { ThreeDmodelsHomeComponent } from './component/main-section/content/3d-models/three-dmodels-home/three-dmodels-home.component';
import { ThreeDmodelsCreateComponent } from './component/main-section/content/3d-models/three-dmodels-create/three-dmodels-create.component';
import { ThreeDmodelsProdMappingComponent } from './component/main-section/content/3d-models/three-dmodels-prod-mapping/three-dmodels-prod-mapping.component';

//#region  rest angular confg
export function RestangularConfigFactory (RestangularProvider) {
  RestangularProvider.setBaseUrl(environment.API_ROOT);
}
//#endregion

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    MainSectionComponent,
    SidenavComponent,
    AmenityComponent,
    DivisionComponent,
    FloorPlanComponent,
    HomeProductComponent,
    PackagesComponent,
    UnitManagerComponent,
    DivisionHomeComponent,
    DivisionCreateComponent,
    SharedHeaderComponent,
    SharedBodyComponent,
    SharedFooterComponent,
    HomeProductHomeComponent,
    HomeProductCreateComponent,
    FloorPlanHomeComponent,
    FloorPlanCreateComponent,
    UnitManagerHomeComponent,
    UnitManagerSubComponent,
    AmenityHomeComponent,
    AmenityCreateComponent,
    PackagesHomeComponent,    
    ThreeDModelsComponent,
    // widgets
    PopupSearchComponent, 
    // search filter
    SearchFilter,
    // modal
    //confirm modal
    ConfirmationModalComponent,
    // only number directive
    OnlyNumber,
    ModalAddRoomComponent,
    ToastNotificationComponent,
    HomeProductEditComponent,
    ModalAddHomeDimensionComponent,
    ModalAddFeaturesComponent,
    ModalFacadeDesignComponent,
    ModalManageFeaturesComponent,
    ModalManageFloorComponent,
    DropdownMenuComponent,
    AddHomeProductModalComponent,
    CreateDimensionComponent,
    FilterModalComponent,
    ThreeDmodelsHomeComponent,
    ThreeDmodelsCreateComponent,
    ThreeDmodelsProdMappingComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    FormsModule,
    PopoverModule.forRoot(),
    ModalModule.forRoot(),
    RestangularModule.forRoot(RestangularConfigFactory),
    CommonModule,
    BrowserAnimationsModule, // required animations module
    ToastrModule.forRoot(), // ToastrModule added,
    OrderModule
  ],
  providers: [
    FooterHeaderDataService,
    RestServices,
    ToastNotificationComponent,
    DataShared,
    // {
    //   provide: BrowserXhr, useClass: NgProgressBrowserXhr
    // }
    RestServices
  ],
  bootstrap: [AppComponent],
  entryComponents: [
    ConfirmationModalComponent,
    ModalAddRoomComponent,
    ModalAddHomeDimensionComponent,
    ModalAddFeaturesComponent,
    ModalFacadeDesignComponent,
    ModalManageFeaturesComponent,
    ModalManageFloorComponent
  ]
})
export class AppModule {
  constructor() {
    if (typeof SVGElement.prototype.contains == 'undefined') {
      SVGElement.prototype.contains = HTMLDivElement.prototype.contains;
    }
  }
}



