import { Component, ViewEncapsulation } from '@angular/core';
import { Router, } from '@angular/router';
import { fail } from 'assert';
import { BoundCallbackObservable } from 'rxjs/observable/BoundCallbackObservable';


@Component({
  selector: 'app-sidenav',
  templateUrl: './sidenav.component.html',
  styleUrls: ['./sidenav.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class SidenavComponent{

  divisionSelected:  boolean = true;
  homeProductSelected: boolean = false;
  floorPlanSelected: boolean = false;
  unitManagerSelected: boolean = false;
  amenitySelected: boolean = false;
  packagesSelected: boolean = false;

  models:boolean = true;

  constructor (private router: Router) {
  }  

  // returns an 'active' value indicating that the nav item is active
  getFragmentClass(fragment: string): string {
    return this.router.url.indexOf(fragment) !== -1  ? 'active' : '';
  }

  getChildNavFragmentClass(fragment: string): string {
    //console.log(this.router);
    return '';
  }

  getUrl() {
    return this.router.url;
  }

  goto(url: string) {
    this.router.navigate([url]);
  }

  getToggleState(param: string): string {
    return this.router.url.indexOf(param) !== -1  ? 'show' : '';
  }
}



