export class HomeProduct {
    home_design_id: number;
    has_media: boolean;
    home_design_name: string;
    design_code: string;
    description: string;
    levels: number;
    base_price: number;
    facades: any[];
    packages: any[];
}

export class Facade {
    date_created: Date;
    date_deleted: Date;
    description: string;
    has_media: boolean;
    home_design_packages_link: HomeDesignPackageLink;
    is_deleted: boolean;
    last_updated: Date;
    space_id: number;
    space_name: string;
    space_type_id: number;
    sub_id: number;
    file: File;
}

export class Package {
    date_created: Date;
    date_deleted: Date;
    description: string;
    has_media: boolean;
    home_design_packages_link: HomeDesignPackageLink;
    id_key: string;
    is_deleted: boolean;
    last_updated: Date;
    package_code: string;
    package_id: number;
    package_name: string;
    package_type: string;
    price: number;
    sub_id: number;
    file: File;
}

export class HomeDesignPackageLink {
    date_created: Date;
    home_design_id: number;
    last_updated: Date;
    link_id: number;
    package_id: number;
}


