import { Component, OnInit, ViewEncapsulation, Input, ViewChild, Output, EventEmitter } from '@angular/core';
import { Router, } from '@angular/router';
import { FooterHeaderDataService } from '../../shared/footer-header-data.service';
import { Location } from '@angular/common';
import { environment } from '../../../../../environments/environment';
import { RestServices } from '../../../../services/rest/rest-services.service';
//import { EventEmitter } from 'selenium-webdriver';

@Component({
  selector: 'app-shared-footer',
  templateUrl: './shared-footer.component.html',
  styleUrls: ['./shared-footer.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class SharedFooterComponent implements OnInit {
  // the items/content
  @Input() Items: any[];
  // the event to listen to when clicking the footer paginations
  @Output() OnPaginated: EventEmitter<any> = new EventEmitter();
  // event: occurs when save&finish button is clicked
  @Output() OnSubmit: EventEmitter<any> = new EventEmitter();
  // for specifying the number of pages
  paginations: number;
  // the buttons
  @ViewChild('prev_page') prev_page;
  @ViewChild('next_page') next_page;
  // for li active class
  selectedIndex = 0;
  liCollections: number[];

  max_rows = environment.tableMaxItem;
  setFooter:any[];

  back() {
    this._location.back();
  }

  constructor(private router: Router, private dataService:FooterHeaderDataService, private _location: Location,
    private rest: RestServices) {
    dataService.varFooter.subscribe(varFooter => {
      this.setFooter = varFooter;
    });  
  }

  selectIndex(selectedIndex: number) {
    if (this.selectedIndex !== selectedIndex) {
      this.selectedIndex = selectedIndex;
      // broadcast the selected page
      this.OnPaginated.emit(selectedIndex);
    }
  } 

  ngOnInit() {
    
  }

  next() {
    if (this.paginations > 1 && ((this.selectedIndex + 1) < this.paginations)){
      this.selectedIndex = this.selectedIndex + 1;
      this.OnPaginated.emit(this.selectedIndex);
    }
  }

  prev() {
    if ((this.selectedIndex - 1) > -1) {
      this.selectedIndex = this.selectedIndex - 1;
      this.OnPaginated.emit(this.selectedIndex);
    }
  }

  save() {
    this.OnSubmit.emit();
  }
  
  updateItems(data: any[]) {
    this.Items = data;
    if (!this.Items) 
      this.Items = [1]; // just to create 1 page

    this.paginations = Math.floor((this.Items.length / this.max_rows)) > 0 ? Math.floor((this.Items.length / this.max_rows)) : 1;
    if ((this.Items.length % this.max_rows !== 0) && this.Items.length > this.max_rows) {
      this.paginations =  this.paginations + 1;
    } 
    this.liCollections = Array(this.paginations).fill(this.paginations)
  }
}



