import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { RestServices } from '../../../../../../../services/rest/rest-services.service';
import { Facade, HomeProduct } from '../../../../../../../model/home-products/home-products-model';
import { ProgressProvider } from '../../../../../shared/data/progress-provider';
import { ToastrService } from 'ngx-toastr';
import { Utilities } from '../../../../../../../services/utils/utilities';

@Component({
  selector: 'app-add-home-product-modal',
  templateUrl: './add-home-product-modal.component.html',
  styleUrls: ['./add-home-product-modal.component.scss']
})
export class AddHomeProductModalComponent implements OnInit {

  @Output() OnSubmit: EventEmitter<any[]> = new EventEmitter();
  @Output() OnClose: EventEmitter<any> = new EventEmitter();

  homeProduct: HomeProduct;

  filename = 'Browse';
  file: File;

  constructor(private rest: RestServices, private toastr: ToastrService) { 

  }

  ngOnInit() {
    this.homeProduct = new HomeProduct();
    this.homeProduct.facades = [];
    this.homeProduct.packages = [];
  }

  submit() {
    console.log(this.homeProduct);
    if (ProgressProvider.isRequesting()) {
      return;
    }
    if (!this.homeProduct.home_design_name) {
      this.toastr.error(Utilities.getToastContentError("Name is required."), '', { closeButton: true, enableHtml: true});
      return;
    }
    ProgressProvider.startLoading();
    this.rest.AddHomeProducts(this.homeProduct, this.file).then((response: any) => {
      if (response.status) {
        this.rest.GET('home_products').then((_data: any) => {
          ProgressProvider.stopLoading();
          this.toastr.success(Utilities.getToastContentSuccess("New product has been successfully added."), '', { closeButton: true, enableHtml: true});
          this.OnSubmit.emit(_data.data.home_designs);
        })
      }
      else {
        this.toastr.error(Utilities.getToastContentError("An error  occured. Please try again."), '', { closeButton: true, enableHtml: true});
      }
    });
  }

  close() {
    this.OnClose.emit(null);
  }

  removeImage() {
    this.filename = 'Browse';
    this.file = null;
  }

  fileChanged(event) {
    this.file = event.srcElement.files[0];
    this.filename = this.file.name;
    console.log(this.filename);
  }
}



