export class SessionData {
    static FacadeSpaceId = 0;

    static FacadeDesigns = [];

    static FeatureItems = [];

    static currentTask = '';

    static HomeDimensions = [];
    static HomeDimensionOperationId = 0;
    static HomeDimensionOperation = 'create';

    static SelectedFloor = null;

    static RoomReturnId = 0;
    static RoomReturnEditId = 0;
    static RoomOperation = 'create';

    static DimensionValue = '';

    static Reset() {
        this.FacadeSpaceId = 0;
        this.FacadeDesigns = [];
        this.FeatureItems = [];
        this.currentTask = '';

        this.HomeDimensions = [];
        this.HomeDimensionOperation = 'create';
        this.HomeDimensionOperationId = 0;
        
        this.SelectedFloor = null;
        this.RoomReturnId = 0;
        this.DimensionValue = '';
    }
}


