import { Injectable } from '@angular/core';

import { FileUploader } from 'ng2-file-upload';

import axios from 'axios';
import { environment } from '../../../environments/environment.prod';
import { error } from 'selenium-webdriver';
import { HomeProduct } from '../../model/home-products/home-products-model';

@Injectable()
export class RestServices {

  constructor() { 
    // this.uploader.onAfterAddingFile = (file) => {
    //   console.log('1', file);
    //   file.withCredentials = false;
    // };

  }
  //#region basic HTTP request
  DELETE(url: string) {
    return new Promise(resolve => {
      return axios.delete(environment.API_ROOT + '/' + url).then(function(data) {
        resolve(data.data);
       }, (error) => {
        resolve(error);
      });
    });
  }

  GET(url: string) {
    return new Promise(resolve => {
      return axios.get(environment.API_ROOT + '/' + url).then(function(data) {
        resolve(data.data);
       }, (error) => {
        resolve(error);
      });
    });
  }

  POST(url: string, _data: any, file: File, file2: File = null, isfile2_a_cad_file: boolean = false) {
    return new Promise(resolve => {
      let formBox = new FormData();
      formBox.append('data', JSON.stringify(_data));
      if (!isfile2_a_cad_file) {
        if (file) {
          formBox.append('file', file);
        }
      }
      else {
        if (file2) {
          formBox.append('cad', file);
        }
        if (file) {
          formBox.append('image', file2);
        }
      }
      
      return axios({
        method: 'post',
        url: environment.API_ROOT + '/' + url,
        data: formBox,
        headers: {'Access-Control-Allow-Origin': '*', 'Content-Type': 'multipart/form-data'}
      })
      .then(function (response) {
       resolve(response.data);
      })
      .catch(function (error) {
        resolve(error);
      });
    });
  }

  PUT(url: string, id: number, _data: any, file: File) {
    return new Promise(resolve => {
      let formData:FormData = new FormData();
      formData.append('data', JSON.stringify(_data));
      if (file) {
        formData.append('file', file);
      }
      url = url.indexOf('/') !== -1 ? url : url + '/';
      return axios({
        method: 'put',
        url: environment.API_ROOT + '/' + url  + id,
        data: formData,
        headers: {'Access-Control-Allow-Origin': '*', 'Content-Type': 'multipart/form-data'}
      })
      .then(function (response) {
       resolve(response.data);
      })
      .catch(function (error) {
        resolve(error);
      });
    });
  }
  //#endregion

  //#region custom request(varies on pages)

  //#region home products custom http request
  AddHomeProducts(_data: HomeProduct, file: File) {
    return new Promise(resolve => {
      let formBox = new FormData();
      formBox.append('data', JSON.stringify(_data))
      formBox.append('file', file);
      return axios({
        method: 'post',
        url: environment.API_ROOT + '/' + 'home_products',
        data: formBox,
        headers: {'Access-Control-Allow-Origin': '*', 'Content-Type': 'multipart/form-data'}
      })
      .then(function (response) {
       resolve(response.data);
      })
      .catch(function (error) {
        resolve(error);
      });
    });
  }
  UpdateHomeProducts(id: number, _data: HomeProduct, file: File) {
    return new Promise(resolve => {
      let formData:FormData = new FormData();
      formData.append('data', JSON.stringify(_data));
      if (file) {
        formData.append('file', file);
      }
      return axios({
        method: 'put',
        url: environment.API_ROOT + '/' + 'home_products/' + id,
        data: formData,
        headers: {'Access-Control-Allow-Origin': '*', 'Content-Type': 'multipart/form-data'}
      })
      .then(function (response) {
       resolve(response.data);
      })
      .catch(function (error) {
        resolve(error);
      });
    });
  }
  //#endregion

  //#endregion
}



