import { Component, OnInit, ViewEncapsulation, ViewChild } from '@angular/core';
import { FooterHeaderDataService } from '../../../shared/footer-header-data.service';
import { SharedHeaderComponent } from '../../../shared/shared-header/shared-header.component'

import { SearchData } from '../../../shared/data/searchData';
import { environment } from '../../../../../../environments/environment';
import { TemplateRef } from '@angular/core/src/linker/template_ref';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal/bs-modal.service';
import { ToastrService } from 'ngx-toastr';
import { Utilities } from '../../../../../services/utils/utilities';
import { RestServices } from '../../../../../services/rest/rest-services.service';
import { ProgressProvider } from '../../../shared/data/progress-provider';
import { SharedFooterComponent } from '../../../shared/shared-footer/shared-footer.component';

@Component({
  selector: 'app-floor-plan-home',
  templateUrl: './floor-plan-home.component.html',
  styleUrls: ['./floor-plan-home.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class FloorPlanHomeComponent implements OnInit {
  @ViewChild('footer') footer: SharedFooterComponent;

  deleteId;
  dataIndex;
  bsModalRef: BsModalRef;

  setHeader:any[] = [true,true,'Floorplan Set','',true,false,false,false,true,'active'];
  setFooter:any[] = ['',true,true,true,false];  

  loadedData: any[];
  filterData = [];
  searchKeys: string = "";

  loading = false;

  paginationIndex = 0;
  constructor(public dataService:FooterHeaderDataService, private modalService: BsModalService,
    private toastr: ToastrService, private rest: RestServices) { 
    dataService.varHeader.next(this.setHeader);
    dataService.varFooter.next(this.setFooter);

    SearchData.OnSearchValueChanged$.subscribe(keys => this.searchData(keys));
  }

  searchData(keys: string): void {
    this.searchKeys = keys;
  }

  ngOnInit() {
    ProgressProvider.startLoading();
    this.loading = true;
    this.rest.GET('floorplans').then((data: any) => {
      this.filterData = data.data.home_designs;
      this.footer.updateItems(this.filterData);

      this.rest.GET("floorplans?limit=" + environment.tableMaxItem + "&skip=" + this.paginationIndex).then((data: any) => {
        console.log('init', data.data);
        this.loadedData = data.data.floorplans;
        this.loading = false;
        ProgressProvider.stopLoading();
      });
    });
  }

  openModal(template: TemplateRef<any>, id: number, index: number) {
    this.deleteId = id;
    this.dataIndex = index;
    this.bsModalRef = this.modalService.show(template);
  }

  deleteItem() {
    // API
    if (ProgressProvider.isRequesting()) {
      return;
    }
    ProgressProvider.startLoading();
    this.rest.DELETE('floorplans/' + this.deleteId).then((data: any) => {
      console.log(data);
      if (data.status) {
        this.loadedData.splice(this.dataIndex, 1);
        this.bsModalRef.hide();
        ProgressProvider.stopLoading();
        this.toastr.success(Utilities.getToastContentSuccess("Item deleted successfully"), '', {closeButton: true, enableHtml: true})
      }
      else {
        this.toastr.error(Utilities.getToastContentError("Unable to delete the item. Please try again"), '', { closeButton: true, enableHtml: true })
      }
    });
  }

  onFooterPaginated(index) {
    console.log(index);
    this.paginationIndex = (index * environment.tableMaxItem);
    ProgressProvider.startLoading();
    this.rest.GET("floorplans?limit=" + environment.tableMaxItem + "&skip=" + this.paginationIndex).then((data: any) => {
      this.loadedData = data.data.home_designs;
      this.loading = false;
      ProgressProvider.stopLoading();
    });
  }
}



