import { Component, OnInit, ViewEncapsulation, Input, ViewChild, Output, EventEmitter} from '@angular/core';
import { Router, } from '@angular/router';
import { FooterHeaderDataService } from '../../shared/footer-header-data.service';
import { Location } from '@angular/common';

import { SearchData } from '../data/searchData';

declare var require: any;

@Component({
  selector: 'app-shared-header',
  templateUrl: './shared-header.component.html',
  styleUrls: ['./shared-header.component.scss'],
  encapsulation: ViewEncapsulation.None
})

export class SharedHeaderComponent implements OnInit {
  createUrl = "";
  backbuttonIcon = require('./assets/icon/backbutton.png');
  filterIcon = require('./assets/icon/filter.png');

  @ViewChild('searhInput') searhInput;

  // header var array
  // ------------------------
  // 0 backbuttonHidden:boolean;
  // 1 forwardslashHidden:boolean;
  // 2 breadcrumb1:string;
  // 3 breadcrumb2:string;
  // 4 breadcrumb2Hidden:boolean;
  // 5 createButtonHidden:boolean;
  // 6 searchBarHidden:boolean;
  // 7 filterBarHidden:boolean;
  // 8 filterButtonHidden:boolean; 
  // 9 breadcrumbState:string = 'active';
  // ------------------------
  setHeader:any[];

  // search input data
  searchData:string;

  createNew() {
    this.router.navigate([this.router.url, "create"]);
  }

  back() {
    this._location.back();
  }

  goBack() {
    if (!this.setHeader[4]) {
      this._location.back();
    }
  }

  constructor(private router: Router,private dataService:FooterHeaderDataService, private _location: Location) {
    dataService.varHeader.subscribe(varHeader => {
      this.setHeader = varHeader;
    });    
    
   }

  ngOnInit() {  
  }
  
  updateSearchKeys() {
    SearchData.changeData(this.searhInput.nativeElement.value);
  }
}



