import { Component, OnInit, ViewEncapsulation, ViewChild, ElementRef } from '@angular/core';
import { FooterHeaderDataService } from '../../../shared/footer-header-data.service';
import { DataShared } from '../data';
import { NgStyle } from '@angular/common';

// imports for modal
import { BsModalService, ModalDirective  } from 'ngx-bootstrap/modal';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';

import { NgModel } from '@angular/forms';

import { environment } from '../../../../../../environments/environment';

import { ConfirmationModalComponent } from '../../../shared/confirm-modal/confirm-modal';
import { PopupSearchData } from '../../../../../model/popp-search/popup-search-model';
import { ProgressProvider } from '../../../shared/data/progress-provider';
import { RestServices } from '../../../../../services/rest/rest-services.service';
import { HomeProduct, Facade } from '../../../../../model/home-products/home-products-model';
import { ActivatedRoute } from '@angular/router';
import { FloorPlan } from '../../../../../model/floorplan/floorplan-model';
import { SessionData } from '../../../../../model/data/data-session-holder';
import { Feature } from '../../../../../model/floorplan/features';
import { ToastrService } from 'ngx-toastr';
import { Utilities } from '../../../../../services/utils/utilities';
import { HomeDimension } from '../../../../../model/floorplan/home-dimension';
import { Floor } from '../../../../../model/floorplan/floor';
import { SpaceType } from '../../../../../model/space_types';
import { Location } from '@angular/common';
import { PopupSearchComponent } from '../../../shared/popup-search/popup-search-component';

@Component({
  selector: 'app-floor-plan-create',
  templateUrl: './floor-plan-create.component.html',
  styleUrls: ['./floor-plan-create.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class FloorPlanCreateComponent implements OnInit {
  operationMode = 'create';
  editId: string;

  bsModalRef: BsModalRef;

  setHeader: any[] = [false, false, 'Floorplan Set', 'Create New', false, true, true, true, true, 'disable'];
  loadedData:HomeProduct[] = [];

  searchData = [];
 
  setFooter: any[] = ['footer2', false, true, false, true];
  facadeDesignList = [];
  featureList = [];

  homeProductData: HomeProduct[] = []; // holder of home products
  selectedHomeProduct: any;

  floorPlanData: any;

  facades: any;

  // edit 
  popupInitialData: string;
  constructor(public dataService: FooterHeaderDataService, private modalService: BsModalService, 
    private dataPass: DataShared, private rest: RestServices,  private activatedRoute: ActivatedRoute,
    private toastr: ToastrService, private _location: Location) {
    dataService.varFooter.next(this.setFooter);

    SessionData.Reset();

    this.floorPlanData = new FloorPlan();
    this.floorPlanData.homeProduct = new HomeProduct();

    this.modalService.onHide.subscribe((reason: string) => {
      this.updateDataFromModal();
    });

    this.activatedRoute.params.subscribe(params => {
      this.editId = params['id'];
      this.operationMode = this.editId ? 'edit' : 'create';
      this.setHeader = [false, false, 'Floorplan Set', this.operationMode == 'create' ? 'Create New' : 'Edit', false, true, true, true, true, 'disable'];
      dataService.varHeader.next(this.setHeader);
      if (this.operationMode == 'edit') {
        this.rest.GET('floorplans/' + this.editId).then((response: any) => {
          console.log(response.data);
          if (!response.status) {
            this._location.back();
          }
          else {
            this.floorPlanData = response.data;
            this.updateEditData();
          }
        });
      }
    });
  }

  updateDataFromModal() {
    let _temp = [];
    let _spaces: any[] = [], _features: any[] = [], _homeDimensions: any = [];
    var type = SessionData.currentTask;
    if (type == 'facade') {
      for (let i = 0; i < SessionData.FacadeDesigns.length; i++) {
        _temp.push(SessionData.FacadeDesigns[i].space_name);
        _spaces.push({
          'space_id':SessionData.FacadeDesigns[i].space_id
        });
      }
      this.facadeDesignList = _temp;
      this.floorPlanData.spaces = _spaces;
    }
    else if (type == 'features') {
      for (let i = 0; i < SessionData.FeatureItems.length; i++) {
        _temp.push(SessionData.FeatureItems[i].feature_name);
        _features.push({'feature_id' :SessionData.FeatureItems[i].feature_id});
      }

      this.featureList = _temp;
      this.floorPlanData.features = _features;
    }
    else if (type == 'home_dimension') {
      for (let i = 0; i < SessionData.HomeDimensions.length; i++) {
        console.log('home_dimension');
        console.log(SessionData.HomeDimensions);
        _temp.push(SessionData.HomeDimensions[i]);
        _homeDimensions.push({
          'dimension': SessionData.HomeDimensions[i].home_dimension ? SessionData.HomeDimensions[i].home_dimension.dimension_name :
          SessionData.HomeDimensions[i].home_dimension_src.dimension_name,
          'dimension_id': SessionData.HomeDimensions[i].space.id}); 
      }

      this.homeDimensionList = _temp;
      this.floorPlanData.dimensions = _homeDimensions;
    }
    else if (type == 'level') {
      console.log(SessionData.SelectedFloor);
    }
    else if (type == 'room') {
      console.log(SessionData.RoomReturnId);
      console.log(SessionData.DimensionValue);

      this.rest.GET('spaces/' + SessionData.RoomReturnId).then((response: any) => {
        console.log(response);
        
        console.log('after response');
        console.log(SessionData.SelectedFloor);
        response.data.dimension = SessionData.DimensionValue;
        this.addChildToLevel(SessionData.SelectedFloor.item_id, response.data);
      });
    }
  }

  @ViewChild('popupSearch_homeProducts') popupSearch_homeProducts;
  updateEditData() {
    // this.popupInitialData = this.floorPlanData.home_design_info.home_design_name;
    // console.log(this.floorPlanData);
    // (this.popupSearch_homeProducts as PopupSearchComponent).setInitialData(this.popupInitialData);

    // // updating facade designs 'facadeDesignList'
    for (var i = 0; i < this.floorPlanData.space_infos.length; i++) {
      if (!this.floorPlanData.space_infos[i].fp_design_spaces_link.level) {
        SessionData.FacadeDesigns.push(this.floorPlanData.space_infos[i]);
      }
    }
    // // console.log(this.facadeDesignList);
    //this.onItemSelected({'id':this.floorPlanData.home_design_id, 'display':this.floorPlanData.home_design_info.home_design_name});
    SessionData.currentTask = 'facade';
    this. updateDataFromModal();

    // updating features
    for (var i = 0; i < this.floorPlanData.feature_infos.length; i++) {
      SessionData.FeatureItems.push(this.floorPlanData.feature_infos[i]);
    }
    SessionData.currentTask = 'features';
    this. updateDataFromModal();

    // updating levels
    for (var i = 0; i < this.floorPlanData.fp_design_level_images.length; i++) {
      let _floor = new Floor();
      _floor.item_id = this.floorPlanData.fp_design_level_images[i].item_id;
      _floor.level = this.floorPlanData.fp_design_level_images[i].level;
      _floor.floor_dimension = this.floorPlanData.fp_design_level_images[i].floor_dimension;
      this.levelsAndRoomsArr.push({'floor': _floor, 'rooms': this.getRoomsFromSpaceInfo(_floor.level) });
    }
    
    console.info('this.levelsAndRoomsArr');
    console.log(this.levelsAndRoomsArr);

    // updating home dimensions
    for (var i = 0; i < this.floorPlanData.home_dimensions_infos.length; i++) {
      let s_data = new PopupSearchData();
      s_data.display = this.floorPlanData.home_dimensions_infos[i].dimension_name;
      s_data.id = this.floorPlanData.home_dimensions_infos[i].dimension_id;
      SessionData.HomeDimensions.push( 
        {
          'id': this.floorPlanData.home_dimensions_infos[i].dimension_id, 
          'home_dimension': this.floorPlanData.home_dimensions_infos[i].fp_design_dimensions_link.dimension, 
          'space': s_data,
          'home_dimension_src':this.floorPlanData.home_dimensions_infos[i]
        });
    }
    SessionData.currentTask = 'home_dimension';
    this.updateDataFromModal();
  }

  getContent(data: any) {
    if (typeof data == 'object') {
      // console.log('object');
      // console.log(data);
      return data.dimension_name;
    }
    else {
      return data;
    }
  }

  getLevelContent(child: any) {
    if (child.dimension) {
      return child.dimension;
    }
    else {
      return child.fp_design_spaces_link.space_dimension;
    }
  }

  deleteHomeProduct(item, index) {
    for (var i = 0; i < SessionData.HomeDimensions.length; i++) {
      if (SessionData.HomeDimensions[i].id == item.id) {
        SessionData.HomeDimensions.splice(i, 1);
        this.homeDimensionList.splice(i, 1);
        break;
      }
    }
  }

  _modalREmoveFromList() {
    for (var i = 0; i < SessionData.HomeDimensions.length; i++) {
      if (SessionData.HomeDimensions[i].id == this.confirmModalDeleteId) {
        SessionData.HomeDimensions.splice(i, 1);
        this.homeDimensionList.splice(i, 1);
        break;
      }
    }
  }
  _modalConfirm() {
    if (this.confirmModalOwner == 'home_dimensions') {
      if (this.confirmModalHomeDimensionServerDelete) {
        ProgressProvider.startLoading();
        console.log(this.confirmModalDeleteId);
        this.rest.DELETE('homedimensions/' + this.confirmModalDeleteId).then((response: any) =>{
          console.log(response);
          ProgressProvider.stopLoading();
          if (response.status) {
            this._modalREmoveFromList();
            this.toastr.success(Utilities.getToastContentSuccess("Item deleted"), '', { closeButton: true, enableHtml: true })
          }
          else {
            this.toastr.error(Utilities.getToastContentError("An error occured. Please try again."), '', { closeButton: true, enableHtml: true })
          }
          this.bsModalRef.hide();
        });
      }
      else {
        this._modalREmoveFromList();
  
        this.bsModalRef.hide();
      }
    }
  }
  confirmModalOwner = '';
  confirmModalDeleteId = 0;
  confirmModalHomeDimensionServerDelete = false;
  modalMessage = '';
  @ViewChild('confirmModal') confirmModal;
  markHomeDimensionForDeletion(item: any, confirmModalOwner: string) {
    if (confirmModalOwner == 'home_dimensions') {
      this.modalMessage = 'Operation cannot be undone. Are you sure to delete this home dimension?';
    }
    this.confirmModalOwner = confirmModalOwner;
    this.confirmModalHomeDimensionServerDelete = item.home_dimension_src;
    console.log(item);
    this.confirmModalDeleteId = !item.home_dimension_src ? item.id : item.home_dimension_src.dimension_id;
    console.log(item);
    this.bsModalRef = this.modalService.show(this.confirmModal);
  }

  editHomeDimension(item) {
    console.log(item);
    SessionData.HomeDimensionOperation = 'edit';
    SessionData.HomeDimensionOperationId = item.home_dimension_src.dimension_id;
    this.bsModalRef = this.modalService.show(ModalAddHomeDimensionComponent);
  }

  ngOnInit() {
    if (this.operationMode == 'create') {
      this.prepareData();
    }
    this.dataPass.featureInputList = [];
    this.dataPass.facadeInputlist = [];
  }

  openConfirmModal() {
    this.bsModalRef = this.modalService.show(ConfirmationModalComponent);
  }

  _onItemSelected(data: any) {
    this.selectedHomeProduct = this.getHomeProductDetail(data.id);
    this.facades = this.selectedHomeProduct.space_infos;
    this.floorPlanData.home_design_id = this.selectedHomeProduct.home_design_id;
    SessionData.FacadeDesigns = this.facades;
    this.updateLevels();
  }
  onItemSelected(data: any) {
    if (this.operationMode == 'edit')  {
      ProgressProvider.startLoading();
      this.rest.GET("home_products").then((response: any) => {
        this.homeProductData = response.data.home_designs;
        ProgressProvider.stopLoading();
        this._onItemSelected(data);
      });
    }
    else {
      this._onItemSelected(data);
    }
    
  }

  homeModalRef: BsModalRef;
  @ViewChild('AddHomeProductModal') AddHomeProductModal;
  onRequestAdd() {
    //console.log('Add Home Product to onRequestAdd()');
    this.homeModalRef = this.modalService.show(this.AddHomeProductModal);
  }

  delete(type, i) {
    if (type == 'level') {
      this.levelList.splice(i, 1);
      return false;
    } else if (type == 'home_dimension') {
      this.homeDimensionList.splice(i, 1);
      return false;
    }
  }
  
  prepareData() {
    ProgressProvider.startLoading();
    this.rest.GET("home_products").then((data: any) => {
      this.homeProductData = data.data.home_designs;
      for (var i = 0; i < this.homeProductData.length; i++) {
        let _d = new PopupSearchData();
        _d.display = this.homeProductData[i].home_design_name;
        _d.id = this.homeProductData[i].home_design_id;
        this.searchData.push(_d);
      }
      ProgressProvider.stopLoading();
    });
  }
  
  // getting an item from home product array
  getHomeProductDetail(id: number): HomeProduct {
    for (let i = 0; i < this.homeProductData.length; i++) {
      if (this.homeProductData[i].home_design_id == id) {
        return this.homeProductData[i];
      }
    }

    return null;
  } 

  // update level potion
  levelsAndRoomsArr: any = [];
  updateLevels() {
    let _temp:any = [];
    if (this.operationMode == 'create') {
      if (this.selectedHomeProduct) {
        console.log(this.floorPlanData.space_infos);
        for (var i = 0; i < this.selectedHomeProduct.levels; i++) {
          let _floor = new Floor();
          _floor.item_id = i;
          _floor.level = i + 1;
          _floor.floor_dimension = '';
          _temp.push({'floor': _floor, 'rooms': [] });
        }
      }
    }

    this.levelsAndRoomsArr = _temp;
    console.log('updateLevels');
  }

  getRoomsFromSpaceInfo(level: number) {
    let _temp = [];
    for (var i = 0; i < this.floorPlanData.space_infos.length; i++) {
      if (this.floorPlanData.space_infos[i].fp_design_spaces_link.level && 
        this.floorPlanData.space_infos[i].fp_design_spaces_link.level == level) {
         _temp.push(this.floorPlanData.space_infos[i]); 
        }
    }

    return _temp;
  }

  addChildToLevel(parent_id: number, data: any) {
    for (var i = 0; i < this.levelsAndRoomsArr.length; i++) {
      if (this.levelsAndRoomsArr[i].floor.item_id == parent_id) {
        console.log(this.levelsAndRoomsArr[i]);
        this.levelsAndRoomsArr[i].rooms.push(data);
        break;
      }
    }

    console.log(this.levelsAndRoomsArr);
  }

  // **
  // call modals below ---------------
  // ** 

  levelList: any[] = this.dataPass.levelList;  
  addRoom(item) {
    SessionData.SelectedFloor = item;
    this.bsModalRef = this.modalService.show(ModalAddRoomComponent);
    this.bsModalRef.content.title = 'Add Room';
    this.bsModalRef.content.filename = 'Browse';
  }

  homeDimensionList: any[] = this.dataPass.homeDimensionList;
  addHomeDimension() {
    SessionData.HomeDimensionOperation = 'create';
    SessionData.HomeDimensionOperationId = 0;
    this.bsModalRef = this.modalService.show(ModalAddHomeDimensionComponent);
    this.bsModalRef.content.filename = 'Browse';
  }

  addFeatures() {
    this.bsModalRef = this.modalService.show(ModalAddFeaturesComponent);    
  }

  manageFeatures() {
    this.bsModalRef = this.modalService.show(ModalManageFeaturesComponent);
  }
  
  facadeDesign() {

    this.bsModalRef = this.modalService.show(ModalFacadeDesignComponent);
    return this.bsModalRef.content.listItem;
  }

  manageFloor() {
    this.bsModalRef = this.modalService.show(ModalManageFloorComponent);
    this.bsModalRef.content.filename = 'Browse';
  }
  editFloor(item) {
    SessionData.SelectedFloor = item;
    SessionData.RoomOperation = 'create';
    SessionData.RoomReturnEditId = 0;
    this.bsModalRef = this.modalService.show(ModalManageFloorComponent);
    this.bsModalRef.content.filename = 'Browse';
  }
  // footer submit listener
  onSubmit() {
    if (ProgressProvider.isRequesting()) {
      return;
    }
    if (this.floorPlanData.home_design_id > 0 &&  this.floorPlanData.fp_design_name) {
      ProgressProvider.startLoading();
      for (var i =0; i < this.levelsAndRoomsArr.length; i++) {
        for (var j = 0; j < this.levelsAndRoomsArr[i].rooms.length; j++) {
          this.floorPlanData.spaces.push(
            {
              "space_id":this.levelsAndRoomsArr[i].rooms[j].space_id,
              "level":this.levelsAndRoomsArr[i].floor.level,
              "space_dimension":this.levelsAndRoomsArr[i].rooms[j].dimension
            });
        }
      }
     
      if (this.operationMode == 'create') {
        this.rest.POST('floorplans', this.floorPlanData, null).then((response: any) => {
          console.log(response);
          if (response.status) {
            SessionData.SelectedFloor.fp_design_id = response.data.fp_design_id;
            // console.log('SessionData.SelectedFloor');
            // console.log(SessionData.SelectedFloor);
            this.rest.POST('floorplanlevels', SessionData.SelectedFloor, SessionData.SelectedFloor.cad, SessionData.SelectedFloor.image, true).then((response: any) => {
              ProgressProvider.stopLoading();
              // console.log(response);
              if (response.status) {
                this.toastr.success(Utilities.getToastContentSuccess("Floor plan added"), '', { closeButton: true, enableHtml: true })
                this._location.back();
              }
              else {
                this.toastr.error(Utilities.getToastContentError("An error occured. Please try again."), '', { closeButton: true, enableHtml: true });
              }
            });
          }
          else {
            ProgressProvider.stopLoading();
            this.toastr.error(Utilities.getToastContentError("An error occured. Please try again."), '', { closeButton: true, enableHtml: true });
          }
        });
      }
      else {
        // edit
        console.log('this.floorPlanData');
        console.log(this.floorPlanData);
        this.rest.PUT('floorplans/', this.floorPlanData.fp_design_id, this.floorPlanData, null).then((response: any) => {
          console.log(response);
          ProgressProvider.stopLoading();
          if (response.status) {
            this.toastr.success(Utilities.getToastContentSuccess("Item updated."), '', { closeButton: true, enableHtml: true })
            this._location.back();
          }
          else {
            this.toastr.error(Utilities.getToastContentError("An error occured. Please try again."), '', { closeButton: true, enableHtml: true })
          }
        });
      }
    }
    else {
      this.toastr.error(Utilities.getToastContentError("Home product and floorplan name are required fields."), '', { closeButton: true, enableHtml: true })
    }
  }

  // home product ceartion modal closes
  onHomeProductClose() {
    this.homeModalRef.hide();
  }

  // when add home product modal is submitted, this will be called
  OnAddHomeProductModalSubmitted(data) {
    this.searchData = [];
    for (var i = 0; i < data.length; i++) {
      let _d = new PopupSearchData();
      _d.display = data[i].home_design_name;
      _d.id = data[i].home_design_id;
      this.searchData.push(_d);
    }

    this.homeModalRef.hide();
  }

  editSpace(item) {
    SessionData.SelectedFloor = item;
    SessionData.RoomOperation = 'edit';
    SessionData.RoomReturnEditId = item.space_id;
    console.log(item);
    this.bsModalRef = this.modalService.show(ModalAddRoomComponent);
    this.bsModalRef.content.title = 'Edit Room';
    this.bsModalRef.content.filename = 'Browse';
  }
}


// *
// modal components
// *

// add room -------------------------
@Component({
  selector: 'modal-add-room',
  templateUrl: './widget-template/add-room/add-room.html'
})
export class ModalAddRoomComponent implements OnInit {
  @ViewChild('childModal') childModal: ModalDirective;
  title: string;
  filename: string;
  loadedData = [];
  searchData = [];

  selectedRoomType: SpaceType = new SpaceType();
  roomData:any;
  operation = 'add'

  operationMode = SessionData.RoomOperation; // edit or add new space
  constructor(public bsModalRef: BsModalRef, private modalService: BsModalService, private dataPass: DataShared,
    private rest: RestServices, private toastr: ToastrService) {
  } 

  ngOnInit() {
    this.roomData = {};
    if (this.operationMode == 'create') {
      ProgressProvider.startLoading();
      this.rest.GET('spaces/types').then((response: any) => {
        console.log(response.data);
        ProgressProvider.stopLoading();
        this.loadedData = response.data.spacetypes;
        this.prepareData();
        this.stateOfButton = Array(this.loadedData.length).fill(false);
        this.stateOfButton2 = Array(this.loadedData.length).fill(false);
      });
    }
    else {
      // edit
      ProgressProvider.startLoading();
      this.rest.GET('spaces/' + SessionData.RoomReturnEditId).then((response: any) => {
        ProgressProvider.stopLoading();
        this.roomData = response.data
        console.log(this.roomData);
      });
    }
  }

  showChildModal(): void {
    this.childModal.show();
    // this.ngOnInit();
    // this.closeInput();
  }

  hideChildModal(): void {
    this.childModal.hide();
    this.closeInput();
  }

  prepareData() {
    for (var i = 0; i < this.loadedData.length; i++) {
      let _d = new PopupSearchData();
      _d.display = this.loadedData[i].type_name;
      _d.id = this.loadedData[i].type_id;
      this.searchData.push(_d);
    }
  }

  onChange(event) {
    var files = event.srcElement.files;
    this.filename = files[0].name;
    this.roomData.file = files[0];
  }

  adjust: string = '';
  normal: string = 'normal';
  addedButtonHidden: boolean = true;
  defaultButtonHidden: boolean = false;
  roomTypeInput: string;

  checkEmpty() {
    if (this.roomTypeInput != '') {
      this.addedButtonHidden = false;
      this.defaultButtonHidden = true;
      this.adjust = 'adjust';
      this.normal = '';
    }
    else if (this.roomTypeInput.length == 0 || this.roomTypeInput == null) {
      this.addedButtonHidden = true;
      this.defaultButtonHidden = false;
      this.adjust = '';
      this.normal = 'normal';
      this.roomTypeInput = null;
    }
  }

  str: string;
  onItemSelected(data: any) {
    console.log(data);
    this.roomData.space_type_id = data.id;
  }

  roomName: string;
  dimension: number;
  imageFile: any;
  submit(roomName, dimension, imageFile) {
    this.dataPass.dataSubmitLevel(this.str, roomName, dimension, imageFile);
    this.bsModalRef.hide();
  }

  closeInput() {
    this.operation = 'add';
    this.addedButtonHidden = true;
    this.defaultButtonHidden = false;
    this.adjust = '';
    this.normal = 'normal';
    this.roomTypeInput = '';
    for (var j = 0; j <= this.loadedData.length; j++) {
      this.stateOfButton2[j] = false;
      this.stateOfButton[j] = false;
    }
    this.listWatcherID = null;
    this.listWatcher = null;
  }

  @ViewChild("input") el: any
  saveRoomType(i,data,listWatcher) {
    if (this.selectedRoomType.type_name) {  
      console.log(this.selectedRoomType);
    
      // if(listWatcher !== null) {
      //   // alert(i);
      //   this.loadedData.splice(i,1,roomType);        
      //   this.listWatcherID = null;
      //   this.listWatcher = null;
      // } else {
      //   this.loadedData.push(roomType);        
      // }
      // this.searchData = [];
      // this.prepareData();
      // this.closeInput();
    }
  }

  saveType() {
    if (this.operation == 'edit') {
      if (this.el.nativeElement.value) {
        console.log(this.selectedRoomType);
        this.selectedRoomType.type_name = this.el.nativeElement.value;
        ProgressProvider.startLoading();
        this.rest.PUT('spaces/types/', this.selectedRoomType.type_id, this.selectedRoomType, null).then((response: any) => {
          console.log(response);
          ProgressProvider.stopLoading();
          if (response.status) {
            this.el.nativeElement.value = '';
            this.searchData = [];
            this.ngOnInit(); 
            this.closeInput();
            this.toastr.success(Utilities.getToastContentSuccess("Item Updated"), '', { closeButton: true, enableHtml: true })
          }
          else {
            this.toastr.error(Utilities.getToastContentError("Unable to update the item. Pleaase try again."), '', { closeButton: true, enableHtml: true })
          }
        });
      }
      else {
        this.toastr.error(Utilities.getToastContentError("Name if required."), '', { closeButton: true, enableHtml: true })
      }
    }
    else {
      if (this.el.nativeElement.value) {
      let _data = new SpaceType();
        _data.type_name = this.el.nativeElement.value;
        this.rest.POST('spaces/types', _data, null).then((response: any) => {
          console.log(response);
          if (response.status) {
            this.el.nativeElement.value = '';
            this.searchData = [];
            this.ngOnInit(); 
            this.closeInput();
            this.toastr.success(Utilities.getToastContentSuccess("Item addded"), '', { closeButton: true, enableHtml: true });
          }
          else {
            this.toastr.error(Utilities.getToastContentError("Unable to add the item. Please try again."), '', { closeButton: true, enableHtml: true })
          }
        });
      }
      else {
        this.toastr.error(Utilities.getToastContentError("Name if required."), '', { closeButton: true, enableHtml: true })
      }
    }
  }

  AddRoomToFloor()  {
    if (this.operationMode == 'create') {
      console.log(this.roomData);
      SessionData.DimensionValue = this.roomData.dimension;
      console.log(SessionData.SelectedFloor);
      if (this.roomData.space_name && this.roomData.space_type_id != 0) {
        ProgressProvider.startLoading();
        this.rest.POST('spaces', this.roomData, this.roomData.file).then((response: any) => {
          ProgressProvider.stopLoading();
          console.log(response);
          if (response.status) {
            SessionData.currentTask = 'room';
            SessionData.RoomReturnId = response.data.space_id;
            this.toastr.success(Utilities.getToastContentSuccess("Item added successfully."), '', { closeButton: true, enableHtml: true })
            this.bsModalRef.hide();
          }
          else {
            this.toastr.error(Utilities.getToastContentError("Unable to add the item. Please try again."), '', { closeButton: true, enableHtml: true })
          }
        });
      }
      else {
        this.toastr.error(Utilities.getToastContentError("Please fill up all required fields."), '', { closeButton: true, enableHtml: true })
      }
    }
    else {
      console.log(this.roomData);
    }
  }

  deleteRoomType(i) {
    this.loadedData.splice(i, 1);
    this.stateOfButton.splice(i, 1);
    this.stateOfButton2.splice(i, 1);
    for (var j = 0; j <= this.loadedData.length; j++) {
      this.stateOfButton2[j] = false;
    }
    this.searchData = [];
    this.prepareData();
    this.closeInput();
    return false;
  }

  stateOfButton: boolean[];
  stateOfButton2: boolean[];
  listWatcher:string = null;
  listWatcherID:number;
  editRoomType(i,item) {
    this.operation = 'edit';
    for(var j = 0; j <= this.loadedData.length; j++){
      if(i != j ){
        this.stateOfButton2[j] = true;
      }
    }
    this.listWatcher = 'onList';
    this.listWatcherID = i;
    this.roomTypeInput = item;
    this.selectedRoomType = item;
    this.addRoomType();
  }
  editState(i): any {
    this.stateOfButton[i] = !this.stateOfButton[i];
  }

  addRoomType() {
    this.addedButtonHidden = false;
    this.defaultButtonHidden = true;
    this.adjust = 'adjust';
    this.normal = '';
  }
}

// add home dimension modal-------------------------
@Component({
  selector: 'modal-add-home-dimension',
  templateUrl: './widget-template/add-home-dimension/add-home-dimension.html',
  styleUrls: ['./widget-template/add-home-dimension/add-home-dimension.css']
})
export class ModalAddHomeDimensionComponent implements OnInit{
  homeDimensionData: HomeDimension;
  spaces: any = [];
  selectedSpace: any;

  operationMode = SessionData.HomeDimensionOperation;

  addDimensionModalRef: BsModalRef;

  constructor(public bsModalRef: BsModalRef, private modalService: BsModalService, private dataPass: DataShared,
  private rest: RestServices, private toastr: ToastrService) { }

  filename: string;
  // used methods
  onChange(event) {
    var files = event.srcElement.files;
    this.filename = files[0].name;
    this.homeDimensionData.file = files[0];
  }

  searchData: PopupSearchData[]; // searching data
  ngOnInit() {
    this.searchData = [];
    this.homeDimensionData = new HomeDimension();
    if (this.operationMode == 'create') {
      ProgressProvider.startLoading();
      this.rest.GET('homedimensions').then((response: any) => {
        ProgressProvider.stopLoading();
        this.spaces = response.data.homedimensions;
        for (let i = 0; i < this.spaces.length; i++) {
          let _temp = new PopupSearchData();
          _temp.display = this.spaces[i].dimension_name;
          _temp.id = this.spaces[i].dimension_id;
          this.searchData.push(_temp);
        }
      });
    }
    else {
      // edit
      ProgressProvider.startLoading();
      this.rest.GET('homedimensions/' + SessionData.HomeDimensionOperationId).then((response: any) => {
        ProgressProvider.stopLoading();
        console.log(response.data[0]);
        this.homeDimensionData = response.data[0];
        this.homeDimensionData.dimension_name =  this.homeDimensionData.fp_design_dimensions_link.dimension;
      });
    }
  }

  spaceName: string;
  dimension: number;
  imageFile: any;
  submit() {
    if (this.operationMode == 'create') {
      var timeStampInMs = window.performance && window.performance.now && window.performance.timing && window.performance.timing.navigationStart ? window.performance.now() + window.performance.timing.navigationStart : Date.now();
      
      if (this.homeDimensionData.dimension_name && this.selectedSpace) {
        SessionData.HomeDimensions.push({'id': timeStampInMs, 'home_dimension': this.homeDimensionData, 'space': this.selectedSpace});
        SessionData.currentTask = 'home_dimension';
        this.toastr.success(Utilities.getToastContentSuccess("Item added successfully."), '', { closeButton: true, enableHtml: true })
        this.bsModalRef.hide();
      }
      else {
        this.toastr.error(Utilities.getToastContentError("All fields are required."), '', { closeButton: true, enableHtml: true })
      }
    }
    else {
      // edit
      if (this.homeDimensionData.dimension_name) {
        this.rest.PUT('homedimensions', SessionData.HomeDimensionOperationId, this.homeDimensionData, null).then((response: any) =>{
          console.log(response);
          if (response.status) {
            this.toastr.success(Utilities.getToastContentSuccess("Item updated successfully."), '', { closeButton: true, enableHtml: true })
            this.bsModalRef.hide();
          }
          else {
            this.toastr.error(Utilities.getToastContentError("Unable to update the item."), '', { closeButton: true, enableHtml: true })
          }
        });
      }
      else {
        this.toastr.error(Utilities.getToastContentError("Dimension is required."), '', { closeButton: true, enableHtml: true })
      }
    }
  }

  onItemSelected(data) {
    console.log(data);
    this.selectedSpace = data;
  }

  @ViewChild('AddDimensionModal') AddDimensionModal;
  onRequestAdd() {
    this.addDimensionModalRef = this.modalService.show(this.AddDimensionModal);
  }

  closeAddDimensionModal() {
    this.addDimensionModalRef.hide();
  }

  submitAddDimensionModal(data: any) {
    console.log(data);
  }
}

