@Component({
  selector: 'modal-add-features',
  templateUrl: './widget-template/add-features/add-features.html'
})
export class ModalAddFeaturesComponent implements OnInit{
  constructor(public bsModalRef: BsModalRef, private modalService: BsModalService, private dataPass: DataShared,
  private rest: RestServices) {
   
  }
  loadedData = [];
  listItem:string[] = [];
  filename: string;
  // used methods  

  ngOnInit() {
    this.rest.GET('features').then((data: any) => {
      this.loadedData = data.data.features;
    });
  }

  hightlightStatus: boolean[] = [];
  checkboxValue: boolean = false;
  classAll: string;
  listDecoy: string[] = [];
  newFunction() {
    if (!this.checkboxValue) { this.classAll = ''; this.listDecoy = []; this.listItem = []; }
    else { this.classAll = 'selectedAll'; this.listDecoy = this.loadedData; this.getItem(true,this.loadedData,0);}
  }
  
  submit() {    
    SessionData.FeatureItems = this.listItem;
    SessionData.currentTask = 'features';
    this.modalService.onHide;
  }

  // temporary on selected item ------------
  getItem(bool,item,index) {
    if(bool) {
      this.listItem.push(item);
    }
    else {
      this.deleteItemInput(item);
    }    
  }

  deleteItemInput(item: string) {
    const index: number = this.listItem.indexOf(item);
    if (index !== -1) {
      this.listItem.splice(index, 1);
    }
  }
  // ---------------------------------------
}

// manage features modal-------------------------
@Component({
  selector: 'modal-manage-features',
  templateUrl: './widget-template/add-features/manage-features.html'
})
export class ModalManageFeaturesComponent implements OnInit {
  @ViewChild('childModal') childModal: ModalDirective;
  @ViewChild('divChild') divChild: any;

  confirmModalRef: BsModalRef;
  constructor(public bsModalRef: BsModalRef, private modalService: BsModalService, private dataPass: DataShared,
    private rest: RestServices, private toastr: ToastrService) {
  }

  selectedFeature: Feature;
  selectedId: number;

  loadedData = [];
  //featureList: any[] = this.dataPass.featureList;
  filename: string;
  // used methods
  ngOnInit() {
    ProgressProvider.startLoading();
    this.rest.GET('features').then((data: any) => {
      ProgressProvider.stopLoading();
      this.loadedData = data.data.features;
      this.stateOfButton = Array(this.loadedData.length).fill(false);
      this.stateOfButton2 = Array(this.loadedData.length).fill(false);
    });
  }

  onChange(event) {
    var files = event.srcElement.files;
    this.filename = files[0].name;
  }

  adjust: string = '';
  normal: string = 'normal';
  addedButtonHidden: boolean = true;
  defaultButtonHidden: boolean = false;
  featureInput: string;
  passFeature: string[] = [];

  checkEmpty() {
    if (this.featureInput != '') {
      this.addedButtonHidden = false;
      this.defaultButtonHidden = true;
      this.adjust = 'adjust';
      this.normal = '';
    }
    else if (this.featureInput.length == 0 || this.featureInput == null) {
      this.addedButtonHidden = true;
      this.defaultButtonHidden = false;
      this.adjust = '';
      this.normal = 'normal';
      this.featureInput = null;
    }
  }

  str: string;
  onItemSelected(data: any) {
    return this.str = data;
  }

  closeInput() {
    this.addedButtonHidden = true;
    this.defaultButtonHidden = false;
    this.adjust = '';
    this.normal = 'normal';
    this.featureInput = '';
    for (var j = 0; j <= this.loadedData.length; j++) {
      this.stateOfButton2[j] = false;
      this.stateOfButton[j] = false;
    }
    this.listWatcherID = null;
    this.listWatcher = null;
  }

  @ViewChild("input") el: ElementRef
  saveFeature(i,item,listWatcher) {
    console.log(i,item,listWatcher);
    // edit
    if (listWatcher) {
      if (item) { 
        this.selectedFeature.feature_name = item;
        ProgressProvider.startLoading();
        this.rest.PUT('features', this.selectedFeature.feature_id, this.selectedFeature, null).then((response: any) => {
          ProgressProvider.stopLoading();
          if (response.status) {
            this.toastr.success(Utilities.getToastContentSuccess("Item updated successfully"), '', { closeButton: true, enableHtml: true })
            
            this.ngOnInit();
            this.closeInput(); 
          }
          else {
            this.toastr.success(Utilities.getToastContentError("Ubale to update the selected item. Please try again."), '', { closeButton: true, enableHtml: true })
          }
        });
      }
      else {
        this.toastr.error(Utilities.getToastContentError("Name is required"), '', { closeButton: true, enableHtml: true })
      }
    }
    else {
      // add 
      if (item) { 
        ProgressProvider.startLoading();
        let _f = new Feature();
        _f.feature_name = item;
        _f.feature_type_id = 28;
        console.log(_f);
        this.rest.POST('features', _f, null).then((response: any) => {
          console.log(response);
          ProgressProvider.stopLoading();
          if (response.status) {
            this.toastr.success(Utilities.getToastContentSuccess("Item added successfully"), '', { closeButton: true, enableHtml: true })
            this.ngOnInit();
            this.closeInput(); 
          }
          else {
            this.toastr.error(Utilities.getToastContentError("An error occured. Please try again."), '', { closeButton: true, enableHtml: true })
          }
        });
      }
      else {
        this.toastr.error(Utilities.getToastContentError("Name is required"), '', { closeButton: true, enableHtml: true })
      }
    }
  }
  
  markForDeletion(template, id) {
    this.selectedId = id;
    this.confirmModalRef = this.modalService.show(template);
  }

  deleteFeature() {
    ProgressProvider.startLoading();
    this.rest.DELETE('features/' + this.selectedId).then((response: any) => {
      ProgressProvider.stopLoading();
      this.confirmModalRef.hide();
      console.log(response);
      if (response.status) {
        this.toastr.success(Utilities.getToastContentSuccess("Item deleted successfully"), '', { closeButton: true, enableHtml: true })
      }
      else {
        this.toastr.error(Utilities.getToastContentError("Unable to delete the item. Please try again."), '', { closeButton: true, enableHtml: true })
      }
    });
  }

  stateOfButton: boolean[];
  stateOfButton2: boolean[];
  listWatcher:string = null;
  listWatcherID:number;
  deleteIndex: number;

  editFeature(i,item) {
    this.selectedFeature = item;
    this.deleteIndex = i;
    console.log(this.selectedFeature);
    for(var j = 0; j <= this.loadedData.length; j++){
      if(i != j ){
        this.stateOfButton2[j] = true;
      }
    }
    this.listWatcher = 'onList';
    this.listWatcherID = item.feature_id;
    this.featureInput = item.feature_name;
    this.addFeature();
  }
  editState(i): any {
    this.stateOfButton[i] = !this.stateOfButton[i];
  }
  addFeature() {
    this.addedButtonHidden = false;
    this.defaultButtonHidden = true;
    this.adjust = 'adjust';
    this.normal = '';
  }
}



