@Component({
  selector: 'modal-manage-floor',
  templateUrl: './widget-template/manage-floor/manage-floor.html'
})
export class ModalManageFloorComponent implements OnInit {
  constructor(public bsModalRef: BsModalRef, private modalService: BsModalService, private dataPass: DataShared,
    private toastr: ToastrService, private rest: RestServices) {

  } 

  floorData: Floor;
  cadfilename: string = 'Browse';
  imgfilename: string = 'Browse';

  // used methods
  onChange(event, type) {
    if (type == 'cad')  {
      this.cadfilename = event.srcElement.files[0].name;
      this.floorData.cad = event.srcElement.files[0];
    }
    else {
      this.imgfilename = event.srcElement.files[0].name;
      this.floorData.image = event.srcElement.files[0];
    }
  }

  ngOnInit() {
    this.floorData = SessionData.SelectedFloor;
  }

  save() {
    if (this.floorData.level && this.floorData.floor_dimension) {
      SessionData.currentTask = 'level';
      this.modalService.onHide;
      this.bsModalRef.hide();
    }
    else {
      this.toastr.error(Utilities.getToastContentError("Level and floor dimensio are required fields."), '', { closeButton: true, enableHtml: true })
    }
  }
}


