import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { HomeDimension } from '../../../../../../../model/floorplan/home-dimension';
import { ToastrService } from 'ngx-toastr';
import { RestServices } from '../../../../../../../services/rest/rest-services.service';
import { ProgressProvider } from '../../../../../shared/data/progress-provider';
import { Utilities } from '../../../../../../../services/utils/utilities';

@Component({
  selector: 'app-create-dimension',
  templateUrl: './create-dimension.component.html',
  styleUrls: ['./create-dimension.component.scss']
})
export class CreateDimensionComponent implements OnInit {

  dimension: HomeDimension;
  dimenUnits:string[];

  @Output() OnClose: EventEmitter<any>;
  @Output() OnSubmit: EventEmitter<any>;

  constructor(private toastr: ToastrService, private rest: RestServices) { 
    this.dimension = new HomeDimension();
    this.dimenUnits = ['m', 'm'];

    this.OnClose = new EventEmitter();
    this.OnSubmit = new EventEmitter();
  }

  ngOnInit() {
  }

  submit() {
    if (ProgressProvider.isRequesting()) {
      return;
    }
    if (this.dimension.dimension_name && this.dimension.dimension_unit) {
      ProgressProvider.startLoading();
      this.rest.POST('homedimensions', this.dimension, null).then((response: any) => {
        ProgressProvider.stopLoading();
        console.log(response.data);
      });
    }
    else {
      this.toastr.error(Utilities.getToastContentError("All fields are required."), '', { closeButton: true, enableHtml: true })
    }
    console.log(this.dimension);
    this.OnSubmit.emit(null);
  }

  close() {
    this.OnClose.emit(null);
  }
}



