
import  NProgress  from 'nprogress';
    
import { EventEmitter, Injectable } from '@angular/core';

@Injectable()
export class ProgressProvider {
    private static _requesting = false;
    constructor() {  }

    static startLoading() {
        this._requesting = true;
        NProgress.start();
    }

    static stopLoading() {
        this._requesting = false;
        NProgress.done();
    }

    static testLoading(duration: number) {
        NProgress.start();
        setTimeout(() => {
            NProgress.done();
        }, duration * 1000);
    }
    static isRequesting(): boolean {
        return this._requesting;
    }
}


