import { EventEmitter, Injectable } from '@angular/core';

@Injectable()
export class SearchData {
    public static OnSearchValueChanged$: EventEmitter<string> = new EventEmitter();

    constructor() {  }

    static changeData(keys: string) {
        this.OnSearchValueChanged$.emit(keys);
    }
}


