import { Component, OnInit } from '@angular/core';
import { ElementRef, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-dropdown-menu',
  templateUrl: './dropdown-menu.component.html',
  styleUrls: ['./dropdown-menu.component.scss'],
  host: {
    '(document:click)': 'onClick($event)',
  },
})
export class DropdownMenuComponent implements OnInit {
  @Input() show = false;
  @Output() OnLostFocus: EventEmitter<any>;
  constructor(private _eref: ElementRef) { 
    this.OnLostFocus = new EventEmitter<any>();
  }

  ngOnInit() {
  }

  onClick(event: any) {
    if (event && !this._eref.nativeElement.contains(event.target) && event.path[1].className !== 'user-menu-label') {
      this.close();
    }
  }

  private close() {
    this.show = false;
    this.OnLostFocus.emit(null);
  }

  logout() {
    this.close();
  }

  settings() {
    this.close();
  }
}



