import { Injectable } from '@angular/core';
import {BehaviorSubject} from 'rxjs/Rx';
import { StateKey } from '@angular/platform-browser/src/browser/transfer_state';

@Injectable()
export class DataShared {

    constructor() { }

    roomType: string;
    roomName : string;
    dimension : number;
    imageFile : any;

    spaceName:string;

    roomTypesList:string[] = ['Bedroom'];

    // FUNCTIONS -------------------------    
    dataSubmitLevel(roomType,roomName,dimension,imageFile) {
        this.levelList.push({roomType,roomName,dimension,imageFile});
    }
    dataSubmitHomeDimension(spaceName,dimension,imageFile) {
        this.homeDimensionList.push({spaceName,dimension,imageFile});
    }
    dataSubmitFeature(feature){
        this.featureList.push(feature);
    }

    // LEVEL  -------------------------
    levelList: ILevel[] = [];


    //HOME DIMENSION  -------------------------
    homeDimensionList: IhomeDimension[] = [];


    // FEATURE -------------------------
    featureList:string[] = ['Study','Walk-in Pantry','Master Bed at Front','Master Bed at Back','Veranda','Closet','Garden','Stairs','Laundry','Basement','Hallway'];
    featureInputList:string[] = [];


    // FACADE DESIGN -------------------------
    facadeList:any[] = ['Khelsey','John','Robert','Nikky'];
    facadeInputlist:any[] = [];
}

// INTERFACE
// LEVEL 
export interface ILevel {
    roomType: string;
    roomName: string;
    dimension: number;
    imageFile: any;
}
//  HOME DIMENSION
export interface IhomeDimension {
    spaceName: string;
    dimension: number;
    imageFile: any;
}


