import { PopupSearchData } from "../../model/popp-search/popup-search-model";

export class Utilities {
    static ToPopupSearchData(srcData: any[], indexAsName: string, indexAsId: string) {
        let data: PopupSearchData[] = [];
        for (var i = 0; i < srcData.length; i++) {
            let _data = new PopupSearchData();

            data.push();
        }
    }

    static getToastContentSuccess(message: string): string {
        return `
        <div class="toast-content">
            <div class="row">
                <div class="col-2" style="padding-right: 0"> <i class="iconned-info-black"></i></div>
                <div class="col-10" style="padding-left: 0!important;">` + message +`</div>
            </div>
        </div>`;
    }

    static getToastContentError(message: string): string {
        return `
        <div class="toast-content">
            <div class="row">
                <div class="col-2" style="padding-right: 0"> <i class="iconned-info-white"></i></div>
                <div class="col-10 white-color" style="padding-left: 0!important; color: white!important;">` + message +`</div>
            </div>
        </div>`;
    }
}


