import { Component, OnInit, ViewEncapsulation, TemplateRef, Injectable } from '@angular/core';
import { FooterHeaderDataService } from '../../../shared/footer-header-data.service';


@Component({
  selector: 'app-division-create',
  templateUrl: './division-create.component.html',
  styleUrls: ['./division-create.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class DivisionCreateComponent implements OnInit {
  filename = "Browse";
  selectedOption = "1";

  // header format
  // ------
  // 1 backbuttonHidden:boolean;
  // 2 forwardslashHidden:boolean;
  // 3 breadcrumb1:string;
  // 4 breadcrumb2:string;
  // 5 breadcrumb2Hidden:boolean;
  // 6 createButtonHidden:boolean;
  // 7 searchBarHidden:boolean;
  // 8 filterBarHidden:boolean;
  // 9 filterButtonHidden:boolean; 
  // 10 breadcrumbState:string = 'active';
  setHeader:any[] = [false,false,'Division','Create New',false,true,true,true,true,'disable'];
  
    // footer format 
    // ------
    // 0 footer2:string;
    // 1 saveFinishButtonHidden:boolean;
    // 2 nextButtonHidden:boolean;
    // 3 backButtonHidden:boolean;
    // 4 paginationHidden:boolean;
    setFooter:any[] = ['footer2',false,true,true,true];  
  
    constructor(public dataService:FooterHeaderDataService) { 
      dataService.varHeader.next(this.setHeader);
      dataService.varFooter.next(this.setFooter);
    }

  ngOnInit() {
  }
  onChange(event) {
    var files = event.srcElement.files;
    this.filename = files[0].name;
  }

  setSelection(index: string) {
    this.selectedOption = index;
  }  
}



