import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// importing main section components
import { DivisionComponent } from './component/main-section/content/division/division.component';
  // sub division
  import { DivisionHomeComponent } from './component/main-section/content/division/division-home/division-home.component';
  import { DivisionCreateComponent } from './component/main-section/content/division/division-create/division-create.component';

import { HomeProductComponent } from './component/main-section/content/home-product/home-product.component';
  // sub home
import { HomeProductHomeComponent } from './component/main-section/content/home-product/home-product-home/home-product-home.component';
import { HomeProductCreateComponent } from './component/main-section/content/home-product/home-product-create/home-product-create.component';
import { HomeProductEditComponent } from './component/main-section/content/home-product/home-edit/home-product-edit/home-product-edit.component';

import { FloorPlanComponent } from './component/main-section/content/floor-plan/floor-plan.component';
  // sub floor plan
  import { FloorPlanHomeComponent } from './component/main-section/content/floor-plan/floor-plan-home/floor-plan-home.component';
  import { FloorPlanCreateComponent } from './component/main-section/content/floor-plan/floor-plan-create/floor-plan-create.component';

import { UnitManagerComponent } from './component/main-section/content/unit-manager/unit-manager.component';
  // sub unit manager
  import { UnitManagerHomeComponent } from './component/main-section/content/unit-manager/unit-manager-home/unit-manager-home.component';
  import { UnitManagerSubComponent } from './component/main-section/content/unit-manager/unit-manager-sub/unit-manager-sub.component';

import { AmenityComponent } from './component/main-section/content/amenity/amenity.component';
// sub amenity
import { AmenityHomeComponent } from './component/main-section/content/amenity/amenity-home/amenity-home.component';
import { AmenityCreateComponent } from './component/main-section/content/amenity/amenity-create/amenity-create.component';

import { PackagesComponent } from './component/main-section/content/packages/packages.component';
import { PackagesHomeComponent } from './component/main-section/content/packages/packages-home/packages-home.component';

// 3d models
import { ThreeDModelsComponent } from './component/main-section/content/3d-models/3d-model.component';
import { ThreeDmodelsHomeComponent } from './component/main-section/content/3d-models/three-dmodels-home/three-dmodels-home.component';
import { ThreeDmodelsCreateComponent } from './component/main-section/content/3d-models/three-dmodels-create/three-dmodels-create.component';
import { ThreeDmodelsProdMappingComponent } from './component/main-section/content/3d-models/three-dmodels-prod-mapping/three-dmodels-prod-mapping.component';

const routes: Routes = [
  {
    path: '', 
    redirectTo: 'division', 
    pathMatch:'full'
  },
  {
    path: 'division',
    component: DivisionComponent,
    children: [
      { path: '', component: DivisionHomeComponent },
      { path: 'create', component: DivisionCreateComponent }
    ]
  },
  {
    path: 'homeproduct',
    component: HomeProductComponent,
    children: [
      { path: '', component: HomeProductHomeComponent },
      { path: 'create', component: HomeProductCreateComponent },
      { path: 'edit/:id', component: HomeProductEditComponent}
    ]
  },
  {
    path: 'floorplan',
    component: FloorPlanComponent,
    children: [
      { path: '', component: FloorPlanHomeComponent },
      { path: 'create', component: FloorPlanCreateComponent },
      { path: 'edit/:id', component: FloorPlanCreateComponent }
    ]
  },
  {
    path: 'unitmanager',
    component: UnitManagerComponent,
    children: [
      { path: '', component: UnitManagerHomeComponent },
      { path: 'create', component: UnitManagerSubComponent }
    ]
  },
  {
    path: 'amenity',
    component: AmenityComponent,
    children: [
      { path: '', component: AmenityHomeComponent },
      { path: 'create', component: AmenityCreateComponent }
    ]
  },
  {
    path: 'packages',
    component: PackagesComponent,
    children: [
      { path: '', component: PackagesHomeComponent }]
  },
  {
    path: '3d-models',
    component: ThreeDModelsComponent,
    children: [
      { path: '', component: ThreeDmodelsHomeComponent },
      { path: 'create', component: ThreeDmodelsCreateComponent },
      { path: 'product_mapping', component: ThreeDmodelsProdMappingComponent }
    ]
  },
  { path: '**', redirectTo: 'division' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



